


























import { ACTIONS } from "@/store";
import Vue from "vue";
import { mapActions } from "vuex";

export default Vue.extend({
  name: "LoginForm",

  data() {
    return {
      user: "",
      room: "",
    };
  },

  methods: {
    ...mapActions([ACTIONS.SET_CONNECION_OPTIONS]),

    async enterRoom() {
      await this.setConnectionOptions({
        user: this.user,
        room: this.room,
      });

      this.$router.push("room");
    },
  },
});
