






















import { ACTIONS } from "@/store";
import Vue from "vue";
import { mapActions, mapGetters } from "vuex";

export default Vue.extend({
  name: "MediaControls",

  beforeDestroy() {
    this.killJason();
  },

  computed: {
    ...mapGetters(["isAudioEnabled", "isVideoEnabled"]),
  },

  methods: {
    ...mapActions([
      ACTIONS.TOGGLE_AUDIO,
      ACTIONS.TOGGLE_VIDEO,
      ACTIONS.KILL_JASON,
    ]),

    decline(): void {
      this.$router.push("/");
    },
  },

  props: {
    className: {
      type: String,
    },
  },
});
